.admin {
	border: thin solid gray;
	border-radius: 2em; }

.admenu {
	list-style-type: none; }

.admin li {
	border-bottom: medium solid gray;
	height: 2em;
	vertical-align: middle;
	background-color: azure;
	width: 50%; }
.available-unit {
	border: 1px solid black;
	border-radius: 5px;
	box-shadow: 3px 3px 5px #939697;
	padding: 5px 10px;
	margin: 1rem; }
#available {
	padding: 35px;
	background-color: white;
	border-radius: 25px; }
#available table {
	background-color: white;
	margin: 10px; }
