	border: 1px solid black;
	border-radius: 5px;
	padding: 0.2rem; }
#available td {
	color: black; }
#available tr {
	margin: 1rem;
	border: 1px solid black;
	border-radius: 5px;
	box-shadow: 3px 3px 5px #888888; }
#top {
	color: white; }
